export const ADD_METADATA = 'ADD_METADATA';
export const CLEAR_METADATA = 'CLEAR_METADATA';
const EMPTY_METADATA = {};
export default (function () {
  let state = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : EMPTY_METADATA;
  let action = arguments.length > 1 ? arguments[1] : undefined;

  switch (action.type) {
    case ADD_METADATA:
      {
        const result = { ...state
        };
        Object.entries(action.value).forEach(_ref => {
          let [key, value] = _ref;
          return result[key] = { ...result[key],
            ...value
          };
        });
        return result;
      }

    case CLEAR_METADATA:
      return EMPTY_METADATA;

    default:
      return state;
  }
}); // Selectors

export const sGetMetadata = state => state.metadata;
export const sGetMetadataById = (state, id) => state.metadata[id];